.IandAViewPort {
}
.IandAViewPort-container {
}
.IandAViewPort-creator {
  margin-top: 4rem;
}
.IandAViewPort-creator-inputs {
}
.IandAViewPort-creator-button {
}
.IandAViewPort-listview {
  margin-top: 2rem;
  overflow-y: scroll;
}
.navigation {
  padding: 8px 30px;
  background-color: white;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-logo {
  font-size: 25px;
}
.nav-logo span {
  font-family: "Shrikhand", cursive;
}
.nav-credit {
  font-size: 15px;
  color: grey;
}
.nav-credit span {
}
.nav-credit span a {
  color: grey;
  text-decoration: none;
}

.card-display{
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: center;
  margin: 1rem 0rem;
}
@media(max-width: 420px){
  .nav-logo {
    font-size: 21px;
  }
  .nav-credit {
    font-size: 11px;
    color: grey;
  }
}