.button-link {
  margin: 10px;
}
.button {
  font-family: "Poppins", sans-serif;
  border-radius: 10px;
  font-weight: 700;
  font-size: 15px;
  outline: 0ch;
  cursor: pointer;
  padding: 8px 16px;
}
.btn-full {
  border: 2px solid rgb(186, 19, 186);
  background-color: rgb(186, 19, 186);
  color: white;
}
.btn-full:hover {
  border: 2px solid purple;
  background-color: purple;
}
.btn-half {
  border: 2px solid rgb(186, 19, 186);
  background-color: rgba(128, 0, 128, 0);
  color: rgb(186, 19, 186);
}
.btn-half:hover {
  border: 2px solid purple;
  background-color: rgb(249, 209, 249);
  color: purple;
}
