.acc {
  margin: 5px;
}
.acc-container {
}
.acc-input {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 10px;
  margin: 20px 0px;
  padding: 20px;
  background-color: white;
}
.acc-input label {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.75rem;
}
.acc-input input,
.acc-input textarea {
  font-family: "Inter", sans-serif;
  margin-top: 20px;
  -webkit-box-flex: 1;
  flex-grow: 1;
  box-sizing: border-box;
  appearance: none;
  box-shadow: none;
  outline: 0px;
  border: none;
  border-radius: 12px;
  height: 48px;
  width: 100%;
  padding: 8px;
  font-size: 16px;
  background-color: rgb(243, 244, 246);
}
.acc-input textarea {
  height: 150px;
}
