.ac {
  margin: 15px;
}
.ac-container {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  max-width: 335px;
  min-width: 250px;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
}
.ac-heading {
    display: flex;
    justify-content: space-between;
}
.chip {
  display: flex;
  align-items: center;
}
.chip-image {
  background-color: burlywood;
  margin-right: 10px;
  padding: 10px 13px;
  border-radius: 50px;
  color: white;
  font-weight: bold;
}
.chip-name-time {
}
.chip-name {
  margin-bottom: 2px;
  font-weight: bold;
  font-size: 18px;
}
.chip-time {
  color: grey;
  font-size: 12px;
}
.ac-action{

}
.ac-action-button{
    cursor: pointer;
    font-size: 12px;
    background-color: rgb(186, 19, 186);
    padding: 4px;
    color: white;
    border-radius: 50px;
}
.ac-action-button:hover{
    background-color: purple;
    background-color: purple;
}
.ac-heading span {
}
.ac-description {
  color: #000000a3;
  margin-top: 20px;
}
.ac-description span {
  line-height: 1.4;
}
@media (max-width: 730px) {
  .ac-container {
    max-width: 100%;
  }
}
